.slider {
	position: relative;
	display: flex;
	justify-content: flex-start;
	max-width: 73.941vw;
	margin: 0 auto;
	z-index: 4;
	display: flex;
	justify-content: center;
	&__img {
		&-first {
			position: absolute;
			left: 3vw;
			bottom: 25vh;
			min-width: 16.528vw;
			max-width: 16.528vw;
			min-height: 36.095vh;
			max-height: 36.095vh;
			transform: rotate(1.1deg);
			background-image: url(../images/sprites/png/first.png);
			background-size: 150%;
			background-position: 50% 15%;
			background-repeat: no-repeat;
			transition: all 1s;
		}
		&-second {
			min-width: 33.056vw;
			max-width: 33.056vw;
			min-height: 76.55vh;
			max-height: 76.55vh;
			transform: rotate(1.5deg);
			background-image: url(../images/sprites/png/main.png);
			background-position: center;
			background-size: 100%;
			background-repeat: no-repeat;
			transition: all 1s;
		}
		&-thrid {
			position: absolute;
			right: 55px;
			bottom: 3vh;
			min-width: 23.958vw;
			max-width: 23.958vw;
			min-height: 25.95vh;
			max-height: 25.95vh;
			transform: rotate(2.03deg);
			background-image: url(../images/sprites/png/second.png);
			background-position: 100% 35%;
			background-size: cover;
			background-repeat: no-repeat;
			transition: all 1s;
		}
	}
}