.footer {
	&__wrapper {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		min-height: 8vh;
		margin-top: 2vh;
		hr {
			width: 3.889vw;
			height: 1px;
			background-color: #000000;
			border: none;
			margin: 0;
		}
		a {
			font-family: TT Travels;
			font-size: 0.764vw;
			letter-spacing: 0.02em;
			color: #000000;
			margin-right: 0.556vw;
			&:last-child {
				margin-right: 0;
				margin-left: 0.556vw;
			}
		}
		
	}
}