@media (max-width: 425px) {
	.header {
		&__nav {
			right: 2vw;
			width: 20px;
			height: 20px;
			display: flex;
			flex-direction: column;
			justify-content: center;
			cursor: pointer;
			&::before {
				content: "";
				display: inline-block;
				width: 20px;
				height: 1px;
				background-color: #000000;
				margin-bottom: 5px;
				cursor: pointer;
			}
			&::after {
				content: "";
				display: inline-block;
				width: 20px;
				height: 1px;
				background-color: #000000;
				cursor: pointer;
			}
			&-menu {
				display: none;
				opacity: 0;
				position: fixed;
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				align-items: center;
				justify-content: space-around;
				max-width: 100vw;
				max-height: 100vh;
				background-color: rgba($color: #000000, $alpha: .8);
				transition: all 1s;
				&-items {
					a {
						font-size: 20px;
						color: $color-white;
					}
				}
			}
		}
	}
	.swiper {
		&-slide {
			&-main {
				font-size: 48px;
			}
		}
	}
	.slider {
		&__img {
			&-first {
				display: none;
			}
			&-second {
				min-width: 273px;
				max-width: 273px;
			}
			&-thrid {
				display: none;
			}
		}
	}
	.animate {
		background-position: 100% 50%;
	}
}