@media (max-width: 1024px) {
	.header {
		&__logo {
			a {
				font-size: 15px;
			}
		}
		&__nav {
			&-menu {
				&-items {
					a {
						font-size: 11px;
					}
				}
			}
		}
	}
	.footer {
		&__wrapper {
			a {
				font-size: 11px;
			}
		}
	}
	.slider {
		&__img {
			&-first {
				left: 3vw;
				background-size: 200%;
			}
			&-thrid {
				background-position: 100% 55%;
			}
		}
	}
}