.swiper {
	position: absolute;
	z-index: 90;
	&-container {
		margin-left: auto;
		margin-right: auto;
		position: relative;
		overflow: hidden;
		list-style: none;
		padding: 0;
		z-index: 1;
		width: 100vw;
		height: 80vh;
	}
	&-wrapper {
		position: relative;
		width: 100%;
		z-index: 1;
		display: flex;
		box-sizing: content-box;
	}
	&-slide {
		flex-shrink: 0;
		width: 100%;
		height: 80vh;
		position: relative;
		transition-property: transform;
		display: flex;
		justify-content: center;
		align-items: center;
		font-family: Schnyder Cond XL Demi;
		font-size: 11vw;
		line-height: 7.500vw;
		mix-blend-mode: darken;
		box-sizing: border-box;
		color:rgba(0, 0, 0, 0);
		-webkit-text-stroke: 1px #580300;
		text-transform: uppercase;
		&-active {
			color: #580300;
			transform: rotate(0);
			z-index: 99;
		}
		&-prev {
			transform: translateX(36vw) rotate(-1.5deg);

		}
		&-next {
			transform: translateX(-36vw) rotate(-1.5deg);
		}
		
	}
}