.header {
	&__wrapper {
		position: relative;
		width: 100%;
		min-height: 6vh;
		display: flex;
		justify-content: space-between;
	}
	&__logo {
		a {
			font-family: GT America;
			font-style: normal;
			font-weight: bold;
			font-size: 1.111vw;
			color: #000000;
			text-decoration: none;
		}
	}
	&__nav {
		position: absolute;
		right: 0;
		z-index: 1000;

		&-menu {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			list-style: none;
			max-width: 4.792vw;
			padding: 0;
			margin: 0;
			&-items {
				a {
					font-family: TT Travels;
					font-style: normal;
					font-weight: 300;
					font-size: 0.764vw;
					text-decoration: none;
					color: #000000;
				}
			}
		}
	}
}